import { useEffect, useState } from "react";
import {
  createBrowserRouter,
  Outlet,
  RouterProvider,
  ScrollRestoration,
} from "react-router-dom";

import "./App.css";

import { Error } from "components/Error";
import Modal from "components/Modal";
import { AdminRedirectPage } from "pages/AdminPage";

import WorldBase from "pages/WorldBase";
import { useAudioEffect } from "client-util/useAudioEffect";

import { ServerInterfaceType } from "sock-server/server-interface/ServerInterface";
import { SockAPIErrorCode, SockAPIError } from "sockapi/errors";
import { useNavStore } from "stores/NavStore";
import { useServerInteractionStore } from "stores/ServerInteractionStore";
import { useUserLogStore } from "stores/UserLogStore";
import { useRedirectListener } from "client-util/useRedirectListener";
import {
  WorldRoutes,
  OuterRoutes,
  useRouteMatch,
  PuzzleRoutes,
} from "components/urls";
import PuzzlePage from "pages/PuzzlePage";
import { useRoundScopeListener } from "stores/RoundStore";

function PageBase() {
  const [stuckError, setStuckError] = useState<SockAPIError | null>(null);
  const [isDisconnectedModalClosed, setIsDisconnectedModalClosed] =
    useState(false);
  const addToUserLog = useUserLogStore((state) => state.addToUserLog);

  const getSockClient = useServerInteractionStore(
    (state) => state.getSockClient
  );

  const sockClient = getSockClient();
  const isRootPage = useRouteMatch("index");
  const isWrapupPage = [
    useRouteMatch("wrapup"),
    useRouteMatch("wrapup_shoutouts"),
    useRouteMatch("wrapup_stats"),
    useRouteMatch("wrapup_ama"),
  ].some((x) => !!x);
  const setRedirectRoute = useNavStore((state) => state.setRedirectRoute);

  useEffect(() => {
    if (sockClient === null) return;

    const cleanupOnError = sockClient.onErrorEvent.addListener((err) => {
      addToUserLog(`Received error "${err.errCode}".`);
    });
    const cleanupAuthEffect = sockClient.addAuthEffect(() => {
      addToUserLog("Connected to server.");
    });
    const cleanupAfterDisconnect = sockClient.serverConnections[
      ServerInterfaceType.MAIN
    ].afterDisconnectEvent.addListener(
      ({ numReconnections, reconnectDelayInSeconds }) => {
        if (numReconnections === 0) {
          addToUserLog("Disconnected from server. Reconnecting...");
        } else if (reconnectDelayInSeconds < 0) {
          setStuckError({ errCode: SockAPIErrorCode.CONNECTION_ERROR });
          addToUserLog("Reconnection failed; giving up.");
        } else {
          addToUserLog(
            `Failed to reconnect. Reconnecting in ${reconnectDelayInSeconds} seconds...`
          );
        }
      }
    );

    return () => {
      cleanupOnError();
      cleanupAuthEffect();
      cleanupAfterDisconnect();
    };
  }, [sockClient, addToUserLog]);

  useEffect(() => {
    if (sockClient === null) return;

    return sockClient.addStuckEffect((err) => {
      setStuckError(err);
    });
  }, [sockClient, setStuckError]);

  useEffect(() => {
    if (isRootPage) setRedirectRoute({ route: "puzzles", replace: true });
  }, [isRootPage, setRedirectRoute]);

  const setHasRouter = useNavStore((state) => state.setHasRouter);

  useEffect(() => {
    setHasRouter(true);
  }, [setHasRouter]);

  useRedirectListener();

  useRoundScopeListener();

  useAudioEffect();

  return (
    <>
      <Modal
        isOpen={
          !isWrapupPage && !isDisconnectedModalClosed && stuckError !== null
        }
        title="Disconnected"
        className="disconnected-modal"
      >
        <div>
          {(() => {
            if (stuckError === null) return null;
            switch (stuckError.errCode) {
              case SockAPIErrorCode.CONNECTION_ERROR:
              case SockAPIErrorCode.AUTH_ERROR:
                return <>Unable to connect to the server.</>;
              case SockAPIErrorCode.TEAM_TEMP_BLOCKED:
                return <>The site is down for maintenance.</>;
              default:
                return <>Sorry, something went wrong.</>;
            }
          })()}
        </div>
        <div>
          You will not be able to receive notifications, submit answers, or load
          new content while disconnected. Please monitor your email or{" "}
          <a href="https://bsky.app/profile/galacticpuzzlehunt.com">
            our Bluesky account
          </a>{" "}
          for updates, and refresh the page at a later time to try reconnecting.
        </div>
        <div>
          <button onClick={() => setIsDisconnectedModalClosed(true)}>OK</button>
        </div>
      </Modal>
      <Outlet />
      <ScrollRestoration getKey={(location) => location.pathname} />
    </>
  );
}

const router = createBrowserRouter([
  {
    path: "/",
    element: <PageBase />,
    errorElement: <Error />,
    children: [
      {
        element: <WorldBase />,
        children: [
          {
            element: <PuzzlePage />,
            children: Object.values(PuzzleRoutes),
          },
          ...Object.values(WorldRoutes),
        ],
      },
      {
        // This should never be accessible from the production site,
        // but it's shorter to type so just redirect it to the
        // exposed endpoint.
        path: "admin",
        element: <AdminRedirectPage />,
      },
      ...Object.values(OuterRoutes),
    ],
  },
]);

function App() {
  return <RouterProvider router={router} />;
}

export default App;
