import { useEffect } from "react";
import { useRouteError, isRouteErrorResponse } from "react-router-dom";
import { Link } from "components/Link";
import { useRedirectListener } from "client-util/useRedirectListener";

export function Error() {
  const error = useRouteError();

  // Needed for the site nav to work.
  useRedirectListener();

  useEffect(() => {
    if (import.meta.env.DEV) {
      console.error(error);
    }
  }, [error]);

  const is404 = isRouteErrorResponse(error) && error.status === 404;
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        gap: "24px",
        alignItems: "center",
        justifyContent: "center",
        textAlign: "center",
        position: "fixed",
        inset: "0",
      }}
    >
      <h1>{is404 ? "Page not found" : "Something went wrong"}</h1>
      <h4>
        <Link route="puzzles">This world is imperfect</Link>
      </h4>
      {is404 ? (
        <div>(This is not a puzzle. Click above to go back.)</div>
      ) : (
        <div>
          (This is not a puzzle. Try refreshing, or{" "}
          <a href="mailto:contact@galacticpuzzlehunt.com">contact us</a>.)
        </div>
      )}
    </div>
  );
}
